.post{
    display: flex;
    align-items: flex-start;
    border-bottom: 1px solid var(--twitter-background);
    padding-bottom: 10px;
}

.post__body{
    flex: 1;
    padding: 10px;
}

.post__body>img{
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    border-radius: 20px;
}

.post__footer{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.post__headerDescription{
    margin-bottom: 10px;
    font-size: 15px;
}

.post__headerText>h3{
    font-size: 15px;
    margin-bottom: 5px;
}

.post__badge{
    font-size: 14px !important;
    color: var(--twitter-color);
}

.post__headerSpecial{
    font-weight: 600;
    font-size: 12px;
    color: grey;
}

.post__avatar{
    padding: 15px 5px 0px 20px;
}